    background-image: -o-linear-gradient(left, #743c18 60%, #1E1007 100%);
    background-image: linear-gradient(to right, #743c18 60%, #1E1007 100%);
    opacity: 0.3;
    font-family: 'MontBold', sans-serif;
    font-size: 131px;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
