.brands__company-container {
    background: #171817;
    width: 100%;
}

.brands__company {
    max-width: 1200px;
    min-width: 320px;
    margin: 0 auto;
}

.brand__content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    padding-top: 60px;
    padding-bottom: 60px;
}



.title__brand .title {
    font-family: 'MontBold', sans-serif;
    font-size: 20px;
    font-weight: 300;
    margin: 0;
    color: #ffffff;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
}


.content__brands {
    margin-top: 60px;
}

.brand img {
    max-width: 80%;
    min-width: 70%;
    height: auto;
    opacity: 0.6;
    -webkit-transition: opacity  0.6s;
    -o-transition: opacity  0.6s;
    transition: opacity  0.6s;
}

.brand img:hover{
    opacity: 1;
}



.brands {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.brand {
    width: 70px;
    -ms-flex-preferred-size: 25%;
        flex-basis: 25%;
    margin-top: 21px;
    text-align: center;
    margin-bottom: 6px;
}

@media screen and (max-width: 768px) {
     .brand {
         -ms-flex-preferred-size: 32%;
             flex-basis: 32%;
     }
    .brands {
        -ms-flex-pack: center;
            justify-content: center;
    }
}


@media screen and  (max-width: 480px){
    .title__brand .title {
        font-family: 'MontLight', sans-serif;
        font-size: 2.2rem;
        text-align: left;
    }
    .brands__company {
        margin-left: 40px;
      margin-right: 40px;
    }
}



