.add__work_plus::before,
.add__work_plus::after,
.social__icon-instagram::before,
.social__icon-instagram::after,
.social__icon-behance::before,
.social__icon-behance::after{
    cursor: pointer;
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 100%;
    -webkit-transition: 0.35s cubic-bezier(0.62, 0.02, 0.34, 1);
    -o-transition: 0.35s cubic-bezier(0.62, 0.02, 0.34, 1);
    transition: 0.35s cubic-bezier(0.62, 0.02, 0.34, 1);
    will-change: opacity;
}

.check__work {
    cursor: pointer;
}

.social__icon-instagram {
    width: 20px;
    height: 20px;
    position: relative;
}
.social__icon-behance {
    width: 14px;
    height: 14px;
    position: relative;
}

.social__icon-instagram {
    margin-left: 15px;
}


.social__icon-instagram:hover::before,
.social__icon-behance:hover::before{
    opacity: 0;
    -webkit-transition-delay: 0.1s;
    -o-transition-delay: 0.1s;
    transition-delay: 0.1s;
}


.social__icon-instagram::before {
    background-image: url('../../sources/sprites/icons/inst-icon.svg');
}

.social__icon-instagram::after {
    background-image: url('../../sources/sprites/icons/inst-icon-hover.svg');
    opacity: 0;
    -webkit-transition-delay: 0.2s;
    -o-transition-delay: 0.2s;
    transition-delay: 0.2s;
}

.social__icon-behance::before{
    background-image: url('../../sources/sprites/icons/behance.svg');
}

.social__icon-behance::after {
    background-image: url('../../sources/sprites/icons/behance-icon-hover.svg');
    opacity: 0;
    -webkit-transition-delay: 0.2s;
    -o-transition-delay: 0.2s;
    transition-delay: 0.2s;
}

.social__icon-instagram:hover::after,
.social__icon-behance:hover::after{
    opacity: 1;
    -webkit-transition-delay: 0s;
    -o-transition-delay: 0s;
    transition-delay: 0s;
}


.social__icon-instagram:hover,
.social__icon-behance:hover {
    -webkit-animation: flash-soft 0.4s forwards linear 0.18s;
    animation: flash-soft 0.4s forwards linear 0.18s;
}



.social__icon-behance {
    cursor: pointer;
}


