.shining-underline {
  position: relative; }

.shining-underline span {
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 100%;
  height: 1px;
  overflow: hidden;
  will-change: transform; }

.shining-underline span::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-transform: scaleX(0);
    -ms-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transform-origin: 100% 0;
    -ms-transform-origin: 100% 0;
    transform-origin: 100% 0;
    background: -webkit-gradient(linear, left top, right top, from(#d99b91), to(#ad6f59));
    background: -webkit-linear-gradient(left, #d99b91, #ad6f59);
    background: -o-linear-gradient(left, #d99b91, #ad6f59);
    background: linear-gradient(90deg, #d99b91, #ad6f59);
    will-change: transform;
    -webkit-transition: -webkit-transform 0.48s cubic-bezier(0.7, 0.3, 0, 1);
    transition: -webkit-transform 0.48s cubic-bezier(0.7, 0.3, 0, 1);
    -o-transition: transform 0.48s cubic-bezier(0.7, 0.3, 0, 1);
    transition: transform 0.48s cubic-bezier(0.7, 0.3, 0, 1);
    transition: transform 0.48s cubic-bezier(0.7, 0.3, 0, 1), -webkit-transform 0.48s cubic-bezier(0.7, 0.3, 0, 1); }

.shining-underline:hover span::before {
  -webkit-transform: scaleX(1);
  -ms-transform: scaleX(1);
  transform: scaleX(1);
  -webkit-transform-origin: 0 0;
  -ms-transform-origin: 0 0;
  transform-origin: 0 0; }



/* General link styles */
.link {
	outline: none;
	text-decoration: none;
	position: relative;
	line-height: 0;
	color: #9e9ba4;
	display: inline-block;
}
/* dside */
.link--dside {
	overflow: hidden;
	padding: 10px 18px;
	line-height: 0;
	color: #242424;
}

.link--dside:hover {
	color: #242424;
}

.link--dside::after {
	content: '';
	position: absolute;
	height: 100%;
	width: 100%;
	top: 0;
	right: 0;
	z-index: 0;
	background: #0c0c0c;
	-webkit-transform: translate3d(0,101%,0);
	transform: translate3d(0,101%,0);
	-webkit-transition: -webkit-transform 0.3s;
	transition: -webkit-transform 0.3s;
	-o-transition: transform 0.3s;
	transition: transform 0.3s;
	transition: transform 0.3s, -webkit-transform 0.3s;
	-webkit-transition-delay: .1s;
	     -o-transition-delay: .1s;
	        transition-delay: .1s;
	-webkit-transition-timing-function: cubic-bezier(0.7,0,0.3,1);
	-o-transition-timing-function: cubic-bezier(0.7,0,0.3,1);
	   transition-timing-function: cubic-bezier(0.7,0,0.3,1);
}

.link--dside:hover::after {
	-webkit-transform: translate3d(0,0,0);
	transform: translate3d(0,0,0);
	background: #ed1d24;
	-webkit-transition-delay: .1s;
	     -o-transition-delay: .1s;
	        transition-delay: .1s;
}

.link--dside span {
	display: block;
	position: relative;
}

.link--dside span::before {
	content: url(Logo2.svg);
	position: absolute;
	color: #fff;
	left: 0;
	overflow: hidden;
	white-space: nowrap;
	width: 100%;
	height: 0%;
	opacity: 0;
    z-index: 1;
	transform: translate3d(0px,-5px,3px);
    -webkit-transform: translate3d(0px,-5px,3px);
	-webkit-transition: all 0.3s;
	-o-transition: all 0.3s;
	transition: all 0.3s;
	-webkit-transition-timing-function: cubic-bezier(0.7,0,0.3,1);
	-o-transition-timing-function: cubic-bezier(0.7,0,0.3,1);
	   transition-timing-function: cubic-bezier(0.7,0,0.3,1);
	-webkit-transition-delay: .15s;
	     -o-transition-delay: .15s;
	        transition-delay: .15s;
}

.link--dside:hover span::before {
    -webkit-transform: translate3d(0,0,0);
	width: 100%;
	opacity: 1;
	-webkit-transition: all 0.3s;
	-o-transition: all 0.3s;
	transition: all 0.3s;
	-webkit-transform: translate3d(0px,0px,0px);
	        transform: translate3d(0px,0px,0px);
	height: 100%;
	-webkit-transition-delay: .1s;
	     -o-transition-delay: .1s;
	        transition-delay: .1s;
}
/* Media Queries */

@media screen and (max-width: 42em) {
	.link--dside { font-size: 3em; }
}
