figure.effect-marley figcaption {
    text-align: right;
    -webkit-transition: all 0.35s;
    -o-transition: all 0.35s;
    transition: all 0.35s;
}

figure.effect-marley h2,
figure.effect-marley p {
    position: absolute;
    right: 30px;
    left: 30px;
    padding: 10px 0;
}


figure.effect-marley p {
    right: 220px;
    bottom: 150px;
    color: #fff;
    -webkit-transform: translate3d(0,100%,0);
    transform: translate3d(0,100%,0);
    font-size: 28px;
    font-family: "MontLight";
    font-weight: 300;
}

figure.effect-marley h2 {
        color: #fff;
    top: 50px;
    left: 50px;
    -webkit-transition: -webkit-transform 0.35s;
    transition: -webkit-transform 0.35s;
    -o-transition: transform 0.35s;
    transition: transform 0.35s;
    transition: transform 0.35s, -webkit-transform 0.35s;
    text-align: left;
    -webkit-transform: translate3d(0,20px,0);
    transform: translate3d(0,20px,0);
    opacity: 0;
}

figure.effect-marley:hover figcaption{ background-color: rgba(15, 21, 30, 0.23); -webkit-transition: all 0.25s; -o-transition: all 0.25s; transition: all 0.25s;}
figure.effect-marley:hover h2 {
    -webkit-transform: translate3d(0,0,0);
    transform: translate3d(0,0,0);
    opacity: 1;
}
figure.effect-marley  img {-webkit-transform: scale(1);-ms-transform: scale(1);transform: scale(1); -webkit-transition: -webkit-transform .3s  ease-in-out; transition: -webkit-transform .3s  ease-in-out; -o-transition: transform .3s  ease-in-out; transition: transform .3s  ease-in-out; transition: transform .3s  ease-in-out, -webkit-transform .3s  ease-in-out;}
figure.effect-marley:hover img {
    -webkit-filter: blur(5px);
    filter: blur(5px);
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05);
    background-color: rgba(15, 21, 30, 0.93);
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    -webkit-transition: .3s filter linear, .3s transform ease-in-out;
    -o-transition: .3s filter linear, .3s transform ease-in-out;
    transition: .3s filter linear, .3s transform ease-in-out;
}

figure.effect-marley h2::after {
    position: absolute;
    top: 100%;
    left: 0;
    width: 20%;
    height: 1px;
    background: -webkit-gradient(linear,left top,right top,from(#d99b91),to(#ad6f59));
    background: -webkit-linear-gradient(left,#d99b91,#ad6f59);
    background: -o-linear-gradient(left,#d99b91,#ad6f59);
    background: -webkit-gradient(linear,left top, right top,from(#d99b91),to(#ad6f59));
    background: linear-gradient(90deg,#d99b91,#ad6f59);
    content: '';
    -webkit-transform: translate3d(0,40px,0);
    transform: translate3d(0,40px,0);
}

figure.effect-marley h2::after,
figure.effect-marley p {
    opacity: 0;
    -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
    transition: opacity 0.35s, -webkit-transform 0.35s;
    -o-transition: opacity 0.35s, transform 0.35s;
    transition: opacity 0.35s, transform 0.35s;
    transition: opacity 0.35s, transform 0.35s, -webkit-transform 0.35s;
}

figure.effect-marley:hover h2::after,
figure.effect-marley:hover p {
    opacity: 1;
    -webkit-transform: translate3d(0,0,0);
    transform: translate3d(0,0,0);
    -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
    transition: opacity 0.35s, -webkit-transform 0.35s;
    -o-transition: opacity 0.35s, transform 0.35s;
    transition: opacity 0.35s, transform 0.35s;
    transition: opacity 0.35s, transform 0.35s, -webkit-transform 0.35s;
}

.grid {
    position: relative;
    list-style: none;
    text-align: center;
    -ms-flex:  1 1 30%;
        flex:  1 1 30%;
}


.grid figure {
    position: relative;
    float: left;
    overflow: hidden;
    margin: 0px 0%;
    min-width: 320px;
    width: 100%;
    text-align: center;
    cursor: pointer;
}

.grid figure img {
    position: relative;
    display: block;
    max-height: 360px;
     max-width: 100%;
     min-width: 100%;
    min-height: 264px;
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
}

.grid figure figcaption {
    padding: 0em;
    color: #fff;
    font-size: 1.25em;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.grid figure figcaption::before,
.grid figure figcaption::after {
    pointer-events: none;
}

.grid figure figcaption,
.grid figure figcaption > a {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

/* Anchor will cover the whole item by default */
/* For some effects it will show as a button */
.grid figure figcaption > a {
    z-index: 1000;
    text-indent: 0%;
    font-size: 12px;
    line-height: 9px;
    color: #fff;
    font-family: "MontBold";
    text-decoration: none;
    white-space: nowrap;
    opacity: 0;
    top: 195px;
    left: -340px;
    -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
    transition: opacity 0.35s, -webkit-transform 0.35s;
    -o-transition: opacity 0.35s, transform 0.35s;
    transition: opacity 0.35s, transform 0.35s;
    transition: opacity 0.35s, transform 0.35s, -webkit-transform 0.35s;
}

.grid figure figcaption > a::before {
    background-image: url("./images/arrow.svg");
}

.grid figure figcaption:hover > a {
    opacity: 1;
   
    -webkit-transform: translate3d(0,0,0);
    transform: translate3d(0,0,0);
    -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
    transition: opacity 0.35s, -webkit-transform 0.35s;
    -o-transition: opacity 0.35s, transform 0.35s;
    transition: opacity 0.35s, transform 0.35s;
    transition: opacity 0.35s, transform 0.35s, -webkit-transform 0.35s;
}




.grid figure h2 {
    word-spacing: -0.15em;
